import React, { useEffect } from "react";
import "./Contact.css";

export default function Contact(props) {
    useEffect(() => {
        document.title = `${props.title} - Contact Us`;
        props.setTitle("Contact Us");
        props.stopLoadingBar();
    });
    return (
        <section id="contact-us">
            <div className="contact-info">
                <h1>Let's Connect</h1>
                <div>
                    Lorem ipsum dolor sit amet consectetur adipisicing elit.
                    Illo fuga, quas dolores nostrum molestias doloremque totam,
                    consequuntur inventore laboriosam, nulla repudiandae quam
                    aspernatur quis ipsam? Eligendi beatae totam provident, ut
                    necessitatibus at itaque, officia veniam maiores adipisci
                    placeat dolore voluptatum.
                </div>
            </div>
            <div className="contact-form-div">
                <form action="#" method="" id="contact-form">
                    <h1>Send a request</h1>
                    <div className="form-input-div">
                        <input
                            type="text"
                            name="name"
                            id="name"
                            placeholder="Enter your name"
                        />
                        <label htmlFor="name">Name:</label>
                    </div>
                    <div className="form-input-div">
                        <input
                            type="email"
                            name="email"
                            id="email"
                            placeholder="Enter your email"
                        />
                        <label htmlFor="email">Email:</label>
                    </div>
                    <div className="form-input-div">
                        <textarea
                            name="textarea"
                            id="message"
                            cols="30"
                            rows="6"
                            placeholder="Say what you want to say..."
                        ></textarea>
                        <label htmlFor="message">Message:</label>
                    </div>
                    <button
                        type="submit"
                        id="contact-form-submit-bt"
                        onClick={(e) => {
                            e.preventDefault();
                        }}
                    >
                        Submit
                    </button>
                </form>
            </div>
        </section>
    );
}
