import React from 'react';
import './Header.css';

export default function Header(props){
    let headerBg = props.headerBg;
    return (
        <>
            <style>
                {`
                    header::before{
                        background-image: url(${headerBg});
                    }
                `}
            </style>
            <header>
                <div className="Header-title">{props.title}</div>
            </header>
        </>
    );
}

Header.defaultProps = {
    headerBg: `${process.env.PUBLIC_URL}/static/images/Home.jpg`
}