import React from "react";
import "./BlogItem.css";

export default function BlogItem(props) {
    const { image, title, description, authorImg, authorName, publisedDate } =
        props;
    return (
        <div className="blog-container" onClick={()=>{props.showPopup()}}>
            <div className="blog-image">
                <img src={image} alt="" />
            </div>
            <div className="blog-info">
                <div className="blog-title">{title}</div>
                <div className="blog-description">{description}</div>
                <div className="blog-author-info">
                    <div className="author-img">
                        <img src={authorImg} alt="" />
                    </div>
                    <div className="author-description">
                        <div className="author-name">{authorName}</div>
                        <div className="publised-date">{publisedDate}</div>
                    </div>
                </div>
            </div>
        </div>
    );
}
