import React, { useEffect } from "react";
import "./Catogories.css";

export default function Catogories(props) {
    useEffect(() => {
        document.title = `${props.title} - Catogories`;
        props.setTitle("Catogories");
        props.stopLoadingBar()
    });
    return (
        <section id="catogories">
            <div className="catogories-div-primary">
                <div className="catogories">
                    <img
                        className="catogories-image"
                        src={process.env.PUBLIC_URL+"/static/images/travel.jpg"}
                        alt=""
                        loading="lazy"
                    />
                    <span className="catogories-title">Travel</span>
                </div>
                <div className="catogories">
                    <img
                        className="catogories-image"
                        src={process.env.PUBLIC_URL+"/static/images/nature.jpg"}
                        alt=""
                        loading="lazy"
                    />
                    <span className="catogories-title">Nature</span>
                </div>
                <div className="catogories">
                    <img
                        className="catogories-image"
                        src={process.env.PUBLIC_URL+"/static/images/india.jpg"}
                        alt=""
                        loading="lazy"
                    />
                    <span className="catogories-title">India</span>
                </div>
                <div className="catogories">
                    <img
                        className="catogories-image"
                        src={process.env.PUBLIC_URL+"/static/images/sports.jpg"}
                        alt=""
                        loading="lazy"
                    />
                    <span className="catogories-title">Sports</span>
                </div>
                <div className="catogories">
                    <img
                        className="catogories-image"
                        src={process.env.PUBLIC_URL+"/static/images/history.jpg"}
                        alt=""
                        loading="lazy"
                    />
                    <span className="catogories-title">History</span>
                </div>
                <div className="catogories">
                    <img
                        className="catogories-image"
                        src={process.env.PUBLIC_URL+"/static/images/technology.jpg"}
                        alt=""
                        loading="lazy"
                    />
                    <span className="catogories-title">Technology</span>
                </div>
            </div>
        </section>
    );
}
