import React, { useEffect, useState } from "react";
import "./Navbar.css";
import { Link } from "react-router-dom";

function NavigationTab({ tabTitle, tabLink, startLoadingBar }) {
    return (
        <Link
            to={tabLink}
            className="navigation-tabs"
            onClick={()=>{startLoadingBar()}}
        >
            <div className="navTabTitle">{tabTitle}</div>
            <div className="navTabBottom">
                <div></div>
                <div></div>
            </div>
        </Link>
    );
}

export default function Navbar(props) {
    // const [activaTab, setActivaTab] = useState(true);
    const [navOnTop, setNavOnTop] = useState(true);
    window.addEventListener("scroll", () => {
        if (window.scrollY > 0) {
            setNavOnTop(false);
        } else {
            setNavOnTop(true);
        }
    });
    useEffect(() => {
        if (navOnTop) {
            document.getElementById("navbar").style.background = "transparent";
        } else {
            document.getElementById("navbar").style.background =
                "rgba(14, 24, 34, 0.54)";
        }
    }, [navOnTop]);

    return (
        <nav id="navbar">
            <div className="Navbar-title">{props.title}</div>
            <div className="navigation">
                <NavigationTab tabTitle={"Home"} tabLink="/" startLoadingBar={props.startLoadingBar}/>
                <NavigationTab tabTitle={"Categories"} tabLink="/catogories" startLoadingBar={props.startLoadingBar}/>
                <NavigationTab tabTitle={"About Us"} tabLink="/about" startLoadingBar={props.startLoadingBar}/>
                <NavigationTab tabTitle={"Contact Us"} tabLink="/contact" startLoadingBar={props.startLoadingBar}/>
            </div>
            <div className="nav-user-button">
                <div id="nav-user-bt-icon" title="User">
                    <i className="fa-solid fa-circle-user"></i>
                </div>
                <span className="tooltip" id="user-tooltip">
                    User
                </span>
            </div>
        </nav>
    );
}
