import React from "react";
import "./PopUp.css";
import { popUpContainer } from "../../App";

function closePopup() {
    document.getElementsByClassName(
        "popup-primary-container"
    )[0].style.opacity = 0;
    document.getElementsByClassName(
        "popup-secondary-container"
    )[0].style.transform = "translate(-50%, -65%)";
    setTimeout(() => {
        popUpContainer.unmount();
    }, 300);
}

export default function PopUp(props) {
    let content;
    let bgColor;
    if (props.type === "error") {
        bgColor = "rgb(238, 67, 44)";
        content = (
            <div className="popup-message-sign">
                <i className="fa-regular fa-circle-xmark"></i>
                <h1>Error</h1>
                <p>{props.message}</p>
            </div>
        );
    } else if (props.type === "alert") {
        bgColor = "rgb(255, 193, 7)";
        content = (
            <div className="popup-message-sign">
                <i className="fa-regular fa-triangle-exclamation"></i>
                <h1>Alert</h1>
                <p>{props.message}</p>
            </div>
        );
    } else if (props.type === "message") {
        bgColor = "#257CFD";
        content = (<div className="popup-message-sign">
            <i className="fa-regular fa-circle-exclamation"></i>
            <h1>{props.title}</h1>
            <p>{props.message}</p>
        </div>);
    }

    return (
        <div className="popup-primary-container">
            <div
                className="popup-secondary-container"
                style={{ backgroundColor: bgColor }}
            >
                <span className="popup-close-button" onClick={closePopup}>
                    <i className="fa fa-times"></i>
                </span>
                {content}
            </div>
        </div>
    );
}

PopUp.defaultProps = {
    type: "error",
    title: "Message",
    message: "An unexpected error has occured",
};
