import React, { useEffect, useState } from "react";
import "./Home.css";
import BlogItem from "../BlogItem/BlogItem";

export default function Home(props) {
    const [blogs, setBlogs] = useState([]);
    const blogsUrl = "https://dummyapi.io/data/v1/post?limit=10&page=2";
    const [isLoading, setIsLoading] = useState(false);
    const [loadingIcon, setLoadingIcon] = useState("");

    useEffect(() => {
        fetch(blogsUrl, { headers: { "app-id": "624031df73438f11cb29c0f0" } })
            .then((response) => {
                return response.json();
            })
            .then((data) => {
                setBlogs(data.data);
            });
        document.title = `${props.title} - Home`;
        props.setTitle("Home");
        props.stopLoadingBar();
    }, [props]);

    useEffect(()=>{
        if (!blogs.length){
            setIsLoading(true);
        }
        else{
            setIsLoading(false);
        }
        
    },[blogs])

    useEffect(()=>{
        if (isLoading){
            setLoadingIcon(<i id="loading-icon" className="fa-solid fa-spinner"></i>);
        }
        else{
            setLoadingIcon("")
        }
    },[isLoading])

    
    return (
        <>
            <section id="home">
                <div className="top-mid-container">Most Recent</div>
                <div className="blogs-container">
                    {blogs.map((blog) => {
                        return (
                            <BlogItem
                                key={blog.id}
                                image={blog.image}
                                title={blog.text}
                                description="Blog Description asdf asdf asdf sadf asdf asdf asdf asdf asdf asdf asdf asdf asdf "
                                authorImg={blog.owner.picture}
                                authorName={`${blog.owner.firstName} ${blog.owner.lastName}`}
                                publisedDate={blog.publishDate}
                                showPopup={props.showPopup}
                            />
                        );
                    })}
                </div>
                    {loadingIcon}
            </section>
        </>
    );
}
