import React, { useEffect } from "react";
import "./About.css";

export default function About(props) {
    useEffect(() => {
        document.title = `${props.title} - About Us`;
        props.setTitle("About Us");
        props.stopLoadingBar()
    });
    return (
        <section id="about-me">
            <div className="about-primary-container">
                <div className="about-pic">
                    <img src="static/images/about-me-pic.jpg" alt="" />
                </div>
                <div className="about-details">
                    <div className="about-name">Hrishi Raj</div>
                    <div className="about-desc">
                        Lorem ipsum dolor sit, amet consectetur adipisicing
                        elit. Enim iure delectus illum vero quod illo officia
                        modi ea cum quo tempora quis sed facere culpa mollitia
                        ipsa maiores cumque totam similique commodi, deleniti
                        nisi?
                    </div>
                </div>
            </div>
        </section>
    );
}
