import "./App.css";
import React, {  useRef, useState } from "react";
import { BrowserRouter as Router, Routes, Route } from "react-router-dom";
import Layout from "./components/Layout/Layout";
import Home from "./components/Home/Home";
import Catogories from "./components/Catogories/Catogories";
import About from "./components/About/About";
import Contact from "./components/Contact/Contact";
import LoadingBar from "react-top-loading-bar";
import PopUp from "./components/PopUp/PopUp";
import { createRoot } from "react-dom/client";

var popUpContainer;

function showPopup(type, message){
    popUpContainer =  createRoot(document.getElementById("popup"))
    popUpContainer.render(
        <PopUp type={type} message={message} />
    )
}

function App() {
    const loadingBarRef = useRef(null);
    const headerImageUrls = {
        Home: `${process.env.PUBLIC_URL}/static/images/Home.jpg`,
        Catogories: `${process.env.PUBLIC_URL}/static/images/Catogories.jpg`,
        "About Us": `${process.env.PUBLIC_URL}/static/images/About-Us.jpg`,
        "Contact Us": `${process.env.PUBLIC_URL}/static/images/Contact-Us.jpg`,
    };
    const title = "myBlogs";
    const [headerTitle, setHeaderTitle] = useState("Home");
    return (
        <Router basename={process.env.PUBLIC_URL}>
            <LoadingBar
                color="#f14119"
                ref={loadingBarRef}
            />
            <Routes>
                <Route
                    path="/"
                    element={
                        <Layout
                            title={title}
                            headerTitle={headerTitle}
                            headerImageUrl={headerImageUrls[headerTitle]}
                            loadingBarRef={loadingBarRef}
                        />
                    }
                >
                    <Route
                        index
                        element={
                            <Home
                                title={title}
                                stopLoadingBar={() => {
                                    loadingBarRef.current.complete();
                                }}
                                setTitle={(t) => {
                                    setHeaderTitle(t);
                                }}
                                showPopup={showPopup}
                            />
                        }
                    />
                    <Route
                        path="catogories"
                        element={
                            <Catogories
                                title={title}
                                stopLoadingBar={() => {
                                    loadingBarRef.current.complete();
                                }}
                                setTitle={(t) => {
                                    setHeaderTitle(t);
                                }}
                            />
                        }
                    />
                    <Route
                        path="about"
                        element={
                            <About
                                title={title}
                                stopLoadingBar={() => {
                                    loadingBarRef.current.complete();
                                }}
                                setTitle={(t) => {
                                    setHeaderTitle(t);
                                }}
                            />
                        }
                    />
                    <Route
                        path="contact"
                        element={
                            <Contact
                                title={title}
                                stopLoadingBar={() => {
                                    loadingBarRef.current.complete();
                                }}
                                setTitle={(t) => {
                                    setHeaderTitle(t);
                                }}
                            />
                        }
                    />
                </Route>
            </Routes>
        </Router>
    );
}

export default App;
export { popUpContainer }